:root {
  --color-white: #fff;
  --color-black: #000;
  --color-dark-gray: #10100e;
    --color-yellow: #ffff00;
  --color-blue: #6a83fb;
  --color-violet: #ae72c2;
  --color-red: #f75d83;

  --spacing-ultra-small: 5px;
  --spacing-small: 10px;
  --spacing-medium: 20px;
  --spacing-large: 30px;

  --margin-list: 7px;

  --transition: .4s;

  --font-family-main: sans-serif;

  --font-size-main: 16px;
    --font-size-subheader: 18px;
  --font-size-header: 26px;

  --width-max: 1200px;

  --font-bold: 600;

  --border-radius-small: 3px;
  --border-radius-medium: 5px;

  --text-shadow-yellow:
    0 0 2px var(--color-white),
    0 0 40px var(--color-yellow);

  --text-shadow-blue:
    0 0 2px var(--color-white),
    0 0 40px var(--color-blue);

  --text-shadow-white:
    0 16px 24px 2px rgba(255, 255, 255, 0.02),
    0 6px 30px 5px rgba(255, 255, 255, 0.04),
    0 8px 10px -5px rgba(255, 255, 255, 0.1);

  --text-shadow-black:
    0 16px 24px 2px rgba(0, 0, 0, 0.5),
    0 6px 30px 5px rgba(0, 0, 0, 0.1),
    0 8px 10px -5px rgba(0, 0, 0, 0.2);

  /* THEMES */
  --theme-light-header: #dfe2df;
  --theme-dark-header: #ca676f;
  --theme-neutral-header: #cccc46;
  --theme-default-header: var(--theme-neutral-header);

  --theme-light-bgimage: linear-gradient(to right top, #350857, #33185c, #332461, #342e64, #363766);
  --theme-dark-bgimage: linear-gradient(to right top, #000000, #24090f, #3e0912, #59030e, #710000);
  --theme-neutral-bgimage: url(@static/bg.jpg);
  --theme-default-bgimage: var(--theme-neutral-bgimage);
}

body {
  margin: 0;
  padding: 0;
  background-color: var(--color-dark-gray);
  font-family: var(--font-family-main);
  font-size: var(--font-size-main);
  background-image: var(--theme-default-bgimage);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: top left;
  background-attachment: fixed;
}

*, *::after, *::before {
  box-sizing: border-box;
}

img {
  display: block;
}

.header__text {
  line-height: 1.7;
  font-size: var(--font-size-header);
  text-shadow: 0 0 2px var(--color-black);
  color: var(--theme-default-header);
  transition: color var(--transition);
}